@use '../base/variables' as v;
@use '../base/mixins' as m;

.image-edit{
    width: 100%;
    height: 100%;
    color: v.$white;
    background-color: rgba(0, 0, 0, 0.524);

    display: flex;
    justify-content: center;
    align-items: center;
}

.manage-container {
    margin-top: 5vw;
    text-align: center;
    
    a {
        font-style: none;
        color: v.$gray;
        font-size: 1.2vw;
        border: solid 1px v.$gray;
        margin: 2em 0 1em;
        padding: 0.5vw 2vw;
        font-weight: 600;
    }
    
    a:hover {
        color: v.$white;
        border: solid 1px v.$white;
        cursor: pointer;
    }
    
}

.done-boton{
    a{
        background-color: v.$white;
        
        &:hover{
            background-color: v.$red;
            color: v.$white;
            border: none;
        }
    }
}