@use '../base/variables' as v;
@use '../base/mixins' as m;

.choise-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    max-width: 80%;
    margin: 0 auto;
}

.choise-profile {
    display: flex;
    justify-content: center;
    gap: 2vw;

    //Se ajustan automaticamente
    flex-wrap: wrap;


    .profile-list:hover {
        a{
            .profile{
                border: solid .2vw v.$white;
            }
            p{
                color: v.$white;
            }
        }
    }
    .profile-list {
        text-align: center;
    }



    
    
}
.profile {
    background-image: url(https://occ-0-1479-1480.1.nflxso.net/dnm/api/v6/vN7bi_My87NPKvsBoib006Llxzg/AAAABZfhNS88u5ao0M3F5X4HRBGCFsqdb2nncDt32YQHoM-1BeLJq93H30hWyleqclSwt1jNGm6l0tkeefKiiCOLLL5gNjpSjS_Xlaij.png?r=bd7);
    
    background-size: cover;
    border-radius: .5vw;

    width: 10vw;
    height: 10vw;
    max-height: 200px;
    max-width: 200px;
    min-height: 84px;
    min-width: 84px;



}
.profile-list:nth-child(4) {
    .profile{
        background-image: url(https://occ-0-1479-1480.1.nflxso.net/dnm/api/v6/vN7bi_My87NPKvsBoib006Llxzg/AAAABQ2L2FczTt0lom-DU1nnFyFd_7w5xD0i3yzly2R0qQG3Qn7iNHWv-Gsa-8WHghCLeP5MyGhTsNOspcwoYAqR5x5VwuB972e7PFO-.png?r=25d)
    }
}

.profile-list:last-child {
    .profile {
        background: transparent;
        display: flex;
        justify-content: center;
        align-items: center;

        .profile-circle {
            background-color: v.$gray;
            width: 60%;
            height: 60%;
            border-radius: 50%;
            position: relative;
            display: flex;

            .inner-circle {
                background-color: v.$darkBG;
                position: absolute;
                width: 60%;
                height: 15%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            .profile-more {
                background-color: v.$darkBG;
                position: absolute;
                width: 15%;
                height: 60%;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }

    &:hover{
        .profile{
            background-color: v.$grayLight;
            .inner-circle,.profile-more{
                background-color: v.$grayLight;
            }
        }
    }
}
