html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Helvetica Neue", "Segoe UI", Roboto, Ubuntu, sans-serif;
  font-size: 1.6rem;
  background-color: rgb(20, 20, 20);
}

.animate__animated.animate__fadeIn {
  --animate-duration: .8s;
}

.animate__animated.animate__zoomIn {
  --animate-duration: .7s;
}

.container {
  max-width: 80%;
  margin: 0 auto;
}

.container-forty {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 480px) {
  .container-forty {
    max-width: 40%;
  }
}

.fixed {
  overflow-y: hidden;
}

p {
  color: gray;
  font-family: "Helvetica Neue", "Segoe UI", Roboto, Ubuntu, sans-serif;
  margin: 0.6em 0;
  font-size: 1.3rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.3vw;
  }
}

a {
  text-decoration: none;
}

h1 {
  font-family: "Helvetica Neue", "Segoe UI", Roboto, Ubuntu, sans-serif;
  text-align: center;
  color: #ffffff;
  font-size: 3.5rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3.5vw;
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

.cursor-pointer:hover {
  cursor: pointer;
}

.visibility-hidden {
  visibility: hidden;
}

@font-face {
  font-family: nf-icon;
  font-style: normal;
  font-weight: 400;
  src: url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.eot);
  src: url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.eot?#iefix) format("embedded-opentype"), url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.woff) format("woff"), url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.ttf) format("truetype"), url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.svg#nf-icon-v1-93) format("svg");
}
[class*=" icon-"],
[class^=icon-] {
  font-family: nf-icon;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  line-height: 1;
  text-transform: none;
  transform: translateZ(0);
  color: #ffffff;
}

.choise-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 80%;
  margin: 0 auto;
}

.choise-profile {
  display: flex;
  justify-content: center;
  gap: 2vw;
  flex-wrap: wrap;
}
.choise-profile .profile-list:hover a .profile {
  border: solid 0.2vw #ffffff;
}
.choise-profile .profile-list:hover a p {
  color: #ffffff;
}
.choise-profile .profile-list {
  text-align: center;
}

.profile {
  background-image: url(https://occ-0-1479-1480.1.nflxso.net/dnm/api/v6/vN7bi_My87NPKvsBoib006Llxzg/AAAABZfhNS88u5ao0M3F5X4HRBGCFsqdb2nncDt32YQHoM-1BeLJq93H30hWyleqclSwt1jNGm6l0tkeefKiiCOLLL5gNjpSjS_Xlaij.png?r=bd7);
  background-size: cover;
  border-radius: 0.5vw;
  width: 10vw;
  height: 10vw;
  max-height: 200px;
  max-width: 200px;
  min-height: 84px;
  min-width: 84px;
}

.profile-list:nth-child(4) .profile {
  background-image: url(https://occ-0-1479-1480.1.nflxso.net/dnm/api/v6/vN7bi_My87NPKvsBoib006Llxzg/AAAABQ2L2FczTt0lom-DU1nnFyFd_7w5xD0i3yzly2R0qQG3Qn7iNHWv-Gsa-8WHghCLeP5MyGhTsNOspcwoYAqR5x5VwuB972e7PFO-.png?r=25d);
}

.profile-list:last-child .profile {
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile-list:last-child .profile .profile-circle {
  background-color: gray;
  width: 60%;
  height: 60%;
  border-radius: 50%;
  position: relative;
  display: flex;
}
.profile-list:last-child .profile .profile-circle .inner-circle {
  background-color: rgb(20, 20, 20);
  position: absolute;
  width: 60%;
  height: 15%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.profile-list:last-child .profile .profile-circle .profile-more {
  background-color: rgb(20, 20, 20);
  position: absolute;
  width: 15%;
  height: 60%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.profile-list:last-child:hover .profile {
  background-color: #e5e5e5;
}
.profile-list:last-child:hover .profile .inner-circle, .profile-list:last-child:hover .profile .profile-more {
  background-color: #e5e5e5;
}

.container-new-profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* Solo para fines de visualización */
}
.container-new-profile > * {
  width: 100%;
  /* Otras reglas de estilo para los hijos directos */
}

.header {
  border-bottom: 1px solid gray;
}
.header h1 {
  text-align: left;
  margin: 0;
}

.create-profile-fields {
  padding: 1.5em 0;
  border-bottom: 1px solid gray;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5em;
}
@media (min-width: 768px) {
  .create-profile-fields {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    align-items: center;
  }
}

.field {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1vw;
}
.field.field-text {
  display: flex;
  flex-direction: column;
  position: relative;
}
.field.field-text p {
  color: #e50914;
  font-weight: 400;
}
@media (min-width: 768px) {
  .field.field-text p {
    position: absolute;
    top: calc(100% + 3rem);
    /* Coloca el párrafo debajo del input con un margen */
    left: 50%;
    transform: translateX(-50%);
    font-size: 1.6rem;
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .field.field-text p {
    font-size: 2rem;
  }
}
.field.field-text input {
  width: 100%;
  height: 2em;
  padding: 0.2em 0.6em;
  outline-style: none;
  border-radius: 0.25em;
  background-color: gray;
  border: none;
  font-family: "Helvetica Neue", "Segoe UI", Roboto, Ubuntu, sans-serif;
  font-size: 1.5rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .field.field-text input {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.field.field-text input.error-border {
  border: solid 2px #e50914;
}
@media (min-width: 768px) {
  .field.field-text input {
    font-size: 2.3rem;
  }
}
.field.field-text input::placeholder {
  color: #ffffff;
}
.field.field-checkbox .checkbox {
  user-select: none;
  width: 2.5em;
  height: 2.5em;
  border: solid 1px gray;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.field.field-checkbox .checked {
  color: #fff;
  font-size: 4rem;
}
.field.field-checkbox label {
  font-size: 1.3rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .field.field-checkbox label {
    font-size: 2rem;
  }
}
.field.field-checkbox:hover {
  cursor: pointer;
}

.new-profile-image {
  width: 8vw;
  height: 8vw;
}

.botons-container {
  margin-top: 3.5rem;
  gap: 2rem;
  display: flex;
  flex-direction: column;
  max-width: 100%;
}
@media (min-width: 768px) {
  .botons-container {
    flex-direction: row;
  }
}
.botons-container .boton {
  background-color: transparent;
  font-size: 2.3rem;
  letter-spacing: 2px;
  font-family: "Segoe UI";
  font-weight: 500;
  padding: 0.5em 1.5em;
  text-align: center;
}
.botons-container .boton a {
  color: gray;
}
.botons-container .boton-continue {
  background-color: #ffffff;
}
.botons-container .boton-continue a {
  color: rgb(20, 20, 20);
}
.botons-container .boton-continue.bg-red {
  background-color: #e50914;
}
.botons-container .boton-continue.bg-red a {
  color: #ffffff;
}
.botons-container .boton-continue:hover {
  cursor: pointer;
  background-color: #e50914;
}
.botons-container .boton-continue:hover a {
  color: #ffffff;
}
.botons-container .boton-cancel {
  border: solid 1px gray;
}
.botons-container .boton-cancel:hover {
  border: solid 1px #ffffff;
  cursor: pointer;
}
.botons-container .boton-cancel:hover a {
  color: #ffffff;
}

.image-edit {
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.524);
  display: flex;
  justify-content: center;
  align-items: center;
}

.manage-container {
  margin-top: 5vw;
  text-align: center;
}
.manage-container a {
  font-style: none;
  color: gray;
  font-size: 1.2vw;
  border: solid 1px gray;
  margin: 2em 0 1em;
  padding: 0.5vw 2vw;
  font-weight: 600;
}
.manage-container a:hover {
  color: #ffffff;
  border: solid 1px #ffffff;
  cursor: pointer;
}

.done-boton a {
  background-color: #ffffff;
}
.done-boton a:hover {
  background-color: #e50914;
  color: #ffffff;
  border: none;
}

.animate__animated.animate__backInDown {
  --animate-duration: .5s;
}

.browse-hero-bg {
  background-image: url(https://occ-0-1479-1480.1.nflxso.net/dnm/api/v6/6AYY37jfdO6hpXcMjf9Yu5cnmO0/AAAABRUkbJwNTG0p1FjCUzBvhlwLHrUFzLoFl10gpbOtGZ27MSt8zyMBzXg2IS6xmYQI42W986k6brypXdB_V1c5xKbd-ls_IGowW1O_.webp?r=18e);
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  height: 60vh;
}
@media (min-width: 768px) {
  .browse-hero-bg {
    height: 100vh;
  }
}

.browse-hero img {
  max-height: 100vh;
  object-fit: cover;
  width: 100%;
  object-position: top;
}
@media (min-width: 768px) {
  .browse-hero {
    position: relative;
  }
}

.browse-gradiant {
  position: absolute;
  width: 100%;
  height: 30%;
  background: linear-gradient(transparent, #141414);
  bottom: 0;
  bottom: 0;
  z-index: 1;
}

.browse-hero-info {
  width: 50%;
  position: absolute;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .browse-hero-info {
    left: 25%;
    top: 28%;
  }
}
.browse-hero-info p {
  color: #ffffff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.45);
}
@media (min-width: 768px) {
  .browse-hero-info {
    width: 36%;
    height: auto;
    left: 4%;
    bottom: 30%;
  }
}

.browse-nav {
  background: linear-gradient(rgba(0, 0, 0, 0.821), transparent);
  height: auto;
  padding: 0 4%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  position: sticky;
  width: 100%;
  z-index: 10;
}
@media (max-width: 768px) {
  .browse-nav {
    padding: 2rem 0;
  }
}
@media (min-width: 768px) {
  .browse-nav {
    position: absolute;
    justify-content: space-between;
    height: 68px;
  }
}
.browse-nav.fixed {
  transition: all 0.2s ease;
  position: fixed;
  background-color: rgb(20, 20, 20);
}

.browse-explore-nav {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.browse-explore-nav .icon-netflix {
  width: 10rem;
}
.browse-explore-nav .icon-netflix::before {
  font-size: 25px;
  content: "\e5d0";
  color: #e50914;
}
.browse-explore-nav .browse-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}
.browse-explore-nav .browse-links a {
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 500;
}
.browse-explore-nav .browse-links a:hover {
  transition: color 0.3s;
  color: silver;
}

.browse-secondary-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.browse-secondary-nav .profile-mini {
  width: 32px;
  height: 32px;
  max-height: auto;
  max-width: auto;
  min-height: auto;
  min-width: auto;
  border-radius: 3px;
}
.browse-secondary-nav .browse-nav-profiles {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.browse-secondary-nav .browse-nav-profiles .browse-more-profiles {
  border-color: #fff transparent;
  border-style: solid;
  border-width: 5px 5px 0;
  transition: transform 367ms cubic-bezier(0.21, 0, 0.07, 1);
}
.browse-secondary-nav .icon-nav {
  cursor: pointer;
}

.browse-hero-botons {
  margin-top: 3rem;
  width: 80%;
}
@media (min-width: 768px) {
  .browse-hero-botons {
    display: flex;
    justify-content: left;
    align-items: center;
    gap: 2rem;
  }
}
.browse-hero-botons .browse-hero-boton {
  border-radius: 0.5rem;
  padding: 1rem 0;
  flex: 1;
  background-color: #fff;
  gap: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 768px) {
  .browse-hero-botons .browse-hero-boton {
    margin-bottom: 1rem;
  }
}
.browse-hero-botons .browse-hero-boton .browse-boton-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}
.browse-hero-botons .browse-hero-boton span {
  font-size: 2.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 768px) {
  .browse-hero-botons .browse-hero-boton span {
    font-size: 1.8rem;
  }
}
.browse-hero-botons .browse-hero-boton-info {
  background-color: rgba(109, 109, 110, 0.7);
  color: #ffffff;
}

/*MAIN*/
.browse-main {
  position: relative;
  padding: 0rem 0 0 3%;
  z-index: 100;
}
@media (min-width: 768px) {
  .browse-main {
    margin-top: -15rem;
  }
}

.browse-section-images {
  overflow-x: auto;
  /* Habilita el desplazamiento horizontal */
  white-space: nowrap;
  /* Evita que los elementos se envuelvan a la siguiente línea */
  max-width: 100%;
  margin: 0;
}
.browse-section-images h2 {
  color: #ffffff;
  font-weight: 600;
  margin: 0;
}

.browse-images-container {
  padding: 2rem 0;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 2rem;
  display: inline-flex;
  overflow: hidden;
}
.browse-images-container li {
  width: 100%;
  display: flex;
  overflow-x: auto;
  white-space: nowrap;
  overflow: hidden;
}
.browse-images-container li img:hover {
  cursor: pointer;
}
@media screen and (max-width: 499px) {
  .browse-images-container .image-container {
    min-width: calc(50% - 2rem);
  }
}
@media screen and (min-width: 500px) and (max-width: 799px) {
  .browse-images-container .image-container {
    min-width: calc(33.3333333333% - 2rem);
  }
}
@media screen and (min-width: 800px) and (max-width: 1099px) {
  .browse-images-container .image-container {
    min-width: calc(25% - 2rem);
  }
}
@media screen and (min-width: 1100px) and (max-width: 1399px) {
  .browse-images-container .image-container {
    min-width: calc(20% - 2rem);
  }
}
@media screen and (min-width: 1400px) {
  .browse-images-container .image-container {
    min-width: calc(16.6666666667% - 2rem);
  }
}

