.div-info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgb(0, 0, 0);
  color: #fff;
  padding: 20px;
  box-sizing: border-box;
  transform: translateY(100%);
  /* Empuja el div fuera de la vista inicialmente */
  transition: transform 0.3s ease;
  transform: scale(1);
}

.scrollable-wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  /* Oculta la barra de desplazamiento */
}

.browse-images-container {
  display: flex;
  overflow-x: hidden;
  white-space: nowrap;
}

.scroll-button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  width: auto;
  height: 83%;
  position: absolute;
  z-index: 1;
  font-weight: 800;
  font-family: "Bebas Neue", sans-serif;
  font-size: 3rem;
}
.scroll-button:hover span {
  transform: scale(1.2);
}

.scroll-left {
  left: 0px;
  width: 60px;
  background: linear-gradient(to left, transparent, black);
}

.scroll-right {
  width: 60px;
  right: 0;
  background: linear-gradient(to right, transparent, black);
}

.prueba {
  transform: scale(1.1);
}

.overlay {
  background-color: rgba(0, 0, 0, 0.4588235294);
  width: 100%;
  height: 100%;
  bottom: 0;
  z-index: 100;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.details-movie {
  width: 95%;
  max-width: 850px;
  background-color: #181818;
  position: relative;
}

.buttons-container {
  position: absolute;
  margin-top: -8rem;
  left: 3%;
  display: flex;
  gap: 1rem;
}

.play-button {
  display: flex;
  gap: 1rem;
  background-color: #ededed;
  border: none;
  align-items: center;
  padding: 0.3rem 2rem;
  border-radius: 0.4rem;
}
.play-button .play-text {
  font-size: 2rem;
  font-weight: 600;
}
.play-button:hover {
  cursor: pointer;
}

.like-button {
  background-color: rgba(42, 42, 42, 0.6);
  border: solid 2px #ededed;
  border-radius: 50%;
}
.like-button .like-icon {
  filter: invert(1);
}
.like-button:hover {
  cursor: pointer;
}

.close-button {
  position: absolute;
  top: 3%;
  right: 3%;
  background-color: #181818;
  padding: 1rem;
  border-radius: 50%;
}
.close-button .x-icon {
  filter: invert(1);
}
.close-button:hover {
  cursor: pointer;
}

.metadata-container {
  display: grid;
  grid-template-columns: 66% 1fr;
  padding: 2rem;
  gap: 2rem;
}

.metadata-info {
  display: flex;
  flex-direction: column;
}
.metadata-info .metadata-synopsis {
  font-size: 2rem;
  color: #ffffff;
}

.metadata-info-time {
  color: #9a9a9a;
  display: flex;
  gap: 1rem;
  font-size: 2rem;
  font-family: 600;
  flex-wrap: wrap;
  align-items: center;
}
.metadata-info-time .metadata-maturity {
  border: solid 1px gray;
  padding: 0 1rem;
  width: fit-content;
}
.metadata-info-time .metadata-percent {
  color: #46d369;
}

.metadata-info-right {
  margin: 1.2rem 0;
}
.metadata-info-right p {
  color: #ffffff;
}
.metadata-info-right .tag-metadata {
  color: gray;
}
.metadata-info-right .metadata-info-text {
  font-size: 1.8rem !important;
  margin: 0;
}