@use '../base/variables' as v;
@use '../base/mixins' as m;


.container-new-profile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    > * {
            width: 100%;
            /* Otras reglas de estilo para los hijos directos */
        }
    
    /* Solo para fines de visualización */
}


.header{
    h1{
        text-align: left;
        margin: 0;
    }
    border-bottom: 1px solid v.$gray;


}


.create-profile-fields{
    padding: 1.5em 0;
    border-bottom: 1px solid v.$gray;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 4fr 1fr;
        align-items: center;
    }

    gap: 1.5em;
}

.field{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1vw;
    
    &.field-text{
        display: flex;
        flex-direction: column;
        position: relative;

        p {
            color: v.$red;
            font-weight: 400;
            @include m.tablet {
                position: absolute;
                top: calc(100% + 3rem);
                /* Coloca el párrafo debajo del input con un margen */
                left: 50%;
                transform: translateX(-50%);
                font-size: 1.6rem;
                width: 100%;
            }
            @include m.desktopXL {
                font-size: 2rem;
                
            }
        }
        
        input{

            @include m.tablet {                
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            width: 100%;
            height: 2em;
            padding: .2em .6em;
            outline-style: none;
            border-radius: .25em;
            background-color: gray;
            border: none;
            &.error-border {
                border: solid 2px v.$red;
            }
            font-family: v.$fuente_secundaria;
            font-size: 1.5rem;
            
            
            @include m.tablet {
                font-size: 2.3rem;
            }
            
            color: v.$white;
            &::placeholder{
                color: v.$white;
            }
        }
    }

    &.field-checkbox{
        
        .checkbox{
            user-select: none;
            width: 2.5em;
            height: 2.5em;
            border: solid 1px v.$gray;
            background-color: transparent;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .checked{
            color: #fff;
            font-size: 4rem;
        }

        label{
            font-size: 1.3rem;
        
            @include m.tablet {
        
                font-size: 2rem;
            }
            color: v.$white;
        }

    }

    &.field-checkbox:hover{
        cursor: pointer;
    }
}

.new-profile-image{
    width: 8vw;
    height: 8vw;
}



.botons-container{
    margin-top: 3.5rem;
    gap: 2rem;
    display: flex;
    flex-direction: column;
    max-width: 100%;
    @include m.tablet {    
        flex-direction: row;
    }
    .boton{
        background-color: transparent;
        font-size: 2.3rem;
        letter-spacing: 2px;
        font-family: 'Segoe UI';
        font-weight: 500;
        padding: .5em 1.5em;
        text-align: center;

        a{
            color: v.$gray;
        }
    }

    .boton-continue{
        background-color: v.$white;
        a{
            color: v.$darkBG;
        }

        &.bg-red {
            background-color: v.$red;
            a{
                color: v.$white;

            }
        }

        &:hover{
            cursor: pointer;
            background-color: v.$red;
            a {
                color: v.$white;
            }
        }
    }

    .boton-cancel{
        border: solid 1px v.$gray;

        &:hover{
            border: solid 1px v.$white;
            cursor: pointer;

            a{
                color: v.$white;
            }
        }
    }
}

