/*MAIN*/




// Localizar las peliculas
.browse-main {
    position: relative;
    padding: 0rem 0 0 3%;
    z-index: 100;

    @include m.tablet {
        margin-top: -15rem;
    }
}


// Color al h2
.browse-section-images {
    
    h2 {
        color: v.$white;
        font-weight: 600;
        margin: 0;
    }
    overflow-x: auto;
    /* Habilita el desplazamiento horizontal */
    white-space: nowrap;
    /* Evita que los elementos se envuelvan a la siguiente línea */
    max-width: 100%;
    margin: 0;
}

