.browse-images-container {
    
    // display: flex;
    // gap: v.$separacion;
    // object-fit: contain;
    // overflow-x: hidden;
    // overflow-y: auto;
    padding: 2rem 0;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: v.$separacion;
    display: inline-flex;

    overflow: hidden;


    li{
        width: 100%;
        display: flex;
        overflow-x: auto;
        white-space: nowrap;

        overflow: hidden;
        img:hover{

            cursor: pointer;

        }

    }
    

    @media screen and (max-width: 499px) {
        .image-container {
            min-width: calc((100% / 2) - (v.$separacion));
        }
    }

    @media screen and (min-width: 500px) and (max-width: 799px) {
        .image-container {
            min-width: calc((100% / 3) - (v.$separacion));
        }
    }

    @media screen and (min-width: 800px) and (max-width: 1099px) {
        .image-container {
            min-width: calc((100% / 4) - (v.$separacion));
        }
    }

    @media screen and (min-width: 1100px) and (max-width: 1399px) {
        .image-container {
            min-width: calc((100% / 5) - (v.$separacion));
        }
    }

    @media screen and (min-width: 1400px) {
        .image-container {
            min-width: calc((100% / 6) - (v.$separacion));
        }
    }
}

.image-container {
    position: relative;



    @media screen and (max-width: 499px) {
        min-width: calc((100% / 2) - (v.$separacion));
    }

    @media screen and (min-width: 500px) and (max-width: 799px) {
        min-width: calc((100% / 3) - (v.$separacion));
    }

    @media screen and (min-width: 800px) and (max-width: 1099px) {
        min-width: calc((100% / 4) - (v.$separacion));
    }

    @media screen and (min-width: 1100px) and (max-width: 1399px) {
        min-width: calc((100% / 5) - (v.$separacion));
    }





    @media screen and (min-width: 1400px) {
        min-width: calc((100% / 6) - (v.$separacion));

    }
    transition: all 0.2s ease-out;
    
}

.div-info {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgb(0, 0, 0);
    color: #fff;
    padding: 20px;
    box-sizing: border-box;
    transform: translateY(100%);
    /* Empuja el div fuera de la vista inicialmente */
    transition: transform 0.3s ease;
    transform: scale(1);
}



.scrollable-wrapper {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center ;

    /* Oculta la barra de desplazamiento */
}

.browse-images-container {
    display: flex;
    overflow-x: hidden;
    white-space: nowrap;
}

.scroll-button {
    
    // top: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    // transform: translateY(-50%);

    color: white;
    border: none;
    padding: 5px;
    cursor: pointer;
    width: auto;
    height: 83%;
    position: absolute;
    z-index: 1;
    font-weight: 800;
    font-family: v.$fuente_principal;
    font-size: 3rem;
    &:hover{
        span{
            transform: scale(1.2);
        }
    }
}

.scroll-left {
    left: 0px;
    width: 60px;
    background: linear-gradient(to left, transparent, black);
}

.scroll-right {
    width: 60px;
    right: 0;
    background: linear-gradient(to right, transparent, black);
}

.prueba{

    transform: scale(1.1);

}





