@use 'variables' as v;
@use 'mixins' as m;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_secundaria;
    font-size: 1.6rem;
    background-color: v.$darkBG;

}

.animate__animated.animate__fadeIn {
    --animate-duration: .8s;
}

.animate__animated.animate__zoomIn {
    --animate-duration: .7s;
}

.container{
    max-width: 80%;
    margin: 0 auto;
}

.container-forty{
    width: 80%;
    margin: 0 auto;
    @include m.telefono {
        max-width: 40%;
    }
}

.fixed{
    overflow-y: hidden;
}

p{
    color: v.$gray;
    font-family: v.$fuente_secundaria;
    margin: 0.6em 0;
    font-size: 1.3rem;
    
    @include m.tablet {
        font-size: 1.3vw;
    }
}


a{
    text-decoration: none;
}

h1{
    font-family: v.$fuente_secundaria;
    text-align: center;
    color: v.$white;
    font-size: 3.5rem;
    
    @include m.tablet {
        font-size: 3.5vw;
    }
}


img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    display: block;
}

ul{
    padding: 0;
    margin: 0;
}

li{
    list-style: none;
}

.cursor-pointer{
    &:hover{
        cursor: pointer;
    }
}
.visibility-hidden{
    visibility: hidden;
}

@font-face {
    font-family: nf-icon;
    font-style: normal;
    font-weight: 400;
    src: url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.eot);
    src: url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.eot?#iefix) format("embedded-opentype"), url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.woff) format("woff"), url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.ttf) format("truetype"), url(https://assets.nflxext.com/ffe/siteui/fonts/nf-icon-v1-93.svg#nf-icon-v1-93) format("svg")
}

[class*=" icon-"],
[class^=icon-] {
    // speak: none;
    font-family: nf-icon;
    font-style: normal;
    font-variant: normal;
    font-weight: 400;
    line-height: 1;
    text-transform: none;
    transform: translateZ(0);
    color: v.$white;
}

