@use '../base/variables' as v;
@use '../base/mixins' as m;

.overlay{
    background-color: #00000075;
    width: 100%;
    height: 100%;
    bottom: 0;
    z-index: 100;

    position: fixed;

    display: flex;
    justify-content: center;
    align-items: center;
}

.details-movie{
    width: 95%;
    max-width: 850px;    

    background-color: #181818;

    position: relative;

    
}

.buttons-container{
    position: absolute;
    margin-top: -8rem;
    left: 3%;
    display: flex;
    gap: 1rem;
}

.play-button{
    display: flex;
    gap: 1rem;
    background-color: #ededed;
    border: none;
    align-items: center;

    
    padding: .3rem 2rem;
    border-radius: .4rem;

    .play-text{
        font-size:  2rem;
        font-weight: 600;
    }

    &:hover{
        cursor: pointer;
    }
}

.like-button{
    background-color: rgba(42, 42, 42, .6);;
    border: solid 2px #ededed;
    border-radius: 50%;
    .like-icon{
        filter: invert(1);
    }

    &:hover {
        cursor: pointer;
    }
}

.close-button{
    position: absolute;
    top: 3%;
    right: 3%;
    background-color: #181818;
    padding: 1rem;
    border-radius: 50%;
    .x-icon{
        filter: invert(1);
    }
    &:hover {
        cursor: pointer;
    }
}


.metadata-container{
    display: grid;
    grid-template-columns: 66% 1fr;
    padding: 2rem;
    gap: 2rem;
}

.metadata-info{
    display: flex;
    flex-direction: column;
    .metadata-synopsis{
        font-size: 2rem;
        color: v.$white;
    }
}

.metadata-info-time{
    color: lighten($color: v.$gray, $amount: 10);
    display: flex;
    gap: 1rem;
    font-size: 2rem;
    font-family: 600;
    flex-wrap: wrap;
    align-items: center;
    
    .metadata-maturity{
        border: solid 1px gray;
        padding: 0 1rem;
        width: fit-content;
    }
    
    .metadata-percent{
        color: v.$green;
    }
}

.metadata-info-right{
    margin: 1.2rem 0;
    
    p{
        color: v.$white;
    }

    .tag-metadata{
        color: gray;
    }
    
    .metadata-info-text {
        font-size: 1.8rem !important;
        margin: 0;
    }
}