@use '../base/variables' as v;
@use '../base/mixins' as m;

.animate__animated.animate__backInDown {
    --animate-duration: .5s;
}



.browse-hero-bg{
    background-image: url(https://occ-0-1479-1480.1.nflxso.net/dnm/api/v6/6AYY37jfdO6hpXcMjf9Yu5cnmO0/AAAABRUkbJwNTG0p1FjCUzBvhlwLHrUFzLoFl10gpbOtGZ27MSt8zyMBzXg2IS6xmYQI42W986k6brypXdB_V1c5xKbd-ls_IGowW1O_.webp?r=18e);
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    height: 60vh;
    @include m.tablet {
        height: 100vh;
        
    }
}

.browse-hero{
    img{
        max-height: 100vh;
        object-fit: cover;
        width: 100%;
        object-position: top;
    }
    // position: fixed;
    @include m.tablet {
        // display: inline-block;
        position: relative;
    }
}

.browse-gradiant{
    position: absolute;
    width: 100%;
    height: 30%;
    background: linear-gradient(transparent, #141414);
    // linear-gradient(45deg, blue, red);
    bottom: 0;
    bottom: 0;
    z-index: 1;
}

.browse-hero-info{
    width: 50%;
    position: absolute;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include m.maxtablet {
        
        left: 25%;
        top: 28%;
    }
    p{
        color: v.$white;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, .45);
    }
    
    @include m.tablet {
        
        width: 36%;
        height: auto;
        left: 4%;
        bottom: 30%;
    }
}

.browse-nav{
    background: linear-gradient(rgba(0, 0, 0, 0.821), transparent);
    height: auto;
    padding: 0 4%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;

    position: sticky; 
    width: 100%;
    z-index: 10;
    
    @include m.maxtablet {
        padding: 2rem 0;
    }
    
    @include m.tablet {
        position: absolute;
        justify-content: space-between;
        height: 68px;
    }
    &.fixed {
        transition: all .2s ease;
        position: fixed;
        background-color: v.$darkBG;
    }
}

.browse-explore-nav{

    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .icon-netflix{
        width: 10rem;
        &::before{
        font-size: 25px;
            content: "\e5d0";
            color: v.$red;
        }
    }

    .browse-links{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 1.5rem;
        a{
            color: v.$white;
            font-size: 1.8rem;
            font-weight: 500;
        }
        a:hover{
            transition: color .3s;
            color: lighten(v.$gray, 25);
        }
    }

}

.browse-secondary-nav{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;
    
    .profile-mini{
        width: 32px;
        height: 32px;
        max-height: auto;
        max-width: auto;
        min-height: auto;
        min-width: auto;
        border-radius: 3px;
    }


    .browse-nav-profiles{
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
        .browse-more-profiles{
            border-color: #fff  transparent;
            border-style: solid;
            border-width: 5px 5px 0;

            //transition
            transition: transform 367ms cubic-bezier(.21, 0, .07, 1);
        }
    }


    .icon-nav{
        cursor: pointer;
    }
}

.browse-hero-botons{
    
    @include m.tablet {
        display: flex;
        justify-content: left;
        align-items: center;
        gap: 2rem;
    }
    margin-top: 3rem;
    width: 80%;
    
    .browse-hero-boton{
        border-radius: .5rem;
        padding: 1rem 0;
        flex: 1;
        background-color: #fff;
        gap: .5rem;

        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1rem;
        @include m.maxtablet {
            margin-bottom: 1rem;
        }

        .browse-boton-icon{
            display: flex;
            justify-content: center;
            align-items: center;
        }

        span{
            font-size: 2.3rem;
            display: flex;
            justify-content: center;
            align-items: center;
            @include m.maxtablet {
            font-size: 1.8rem;
            }
        }
    }

    .browse-hero-boton-info{
        background-color: rgba(109, 109, 110, 0.7);
        color: v.$white;
    }
}